body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    font-family: "Source Sans Pro", sans-serif;
    margin: 0;
    padding: 0;
    text-decoration: none;
}

html {
    font-size: 62.5%;
}

.App {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.header {
    font-size: 8rem;
    letter-spacing: 0.1rem;
    font-style: bold;
    color: #40b48e;
    text-shadow: 0 0.2rem 5rem rgb(119, 115, 115);
    text-transform: uppercase;
    margin: 4rem 0;
}

.search-form {
    margin-top: 50px;
    margin-left: 100px;
    margin-right: 100px;
    width: 350px;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #eed91e;
    border-radius: 0.5rem;
    margin-bottom: 5rem;
    box-shadow: 1rem 2rem 5rem #aaa;
}

.alert {
    position: absolute;
    top: -3rem;
    left: 0;
    background-color: rgb(233, 72, 72);
    width: calc(100% - 0.4rem);
    border-radius: 0.5rem;
    padding: 0.2rem;
}

.alert h3 {
    font-size: 1.8rem;
    color: #fff;
    font-weight: 400;
    text-transform: uppercase;
    text-align: center;
}

.search-form input {
    font-size: 1.6rem;
    border: none;
    border-bottom: 0.1rem solid #ccc;
    margin: 0 0.5rem;
    border-radius: 0.5rem;
    height: 3rem;
}

.search-form input[type="text"] {
    padding: 0 0.5rem;
    width: 60%;
    color: #423f3f;
}

.search-form input[type="submit"] {
    width: 25%;
    text-transform: uppercase;
    background-color: #f51818;
    color: #fff;
}

.recipes {
    width: 90%;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
    flex-direction: grid;
}

.bigrecipe {}

.recipe {
    margin-top: 200px;
    width: 30rem;
    margin: 3rem;
    margin-right: 20px;
    display: flex;
    flex-direction: column;
    background-color: #eee675;
    /* background-color: #0c0d0e; */
    padding: 2rem;
    position: relative;
    border-radius: 0.5rem;
    box-shadow: 2rem 3rem 5rem #aaa;
    justify-content: space-between;
}

.recipe h2 {
    background-color: rgba(14, 117, 49, 0.7);
    border-radius: 0.5rem;
    /* padding: 0.3rem 0.5rem;
    position: absolute;
    top: 2.5rem;
    left: 2.5rem;
    right: 2.5rem; */
    font-size: 2.5rem;
    font-weight: 400;
    text-shadow: 0 0.5rem 0.5rem #555;
    color: #fff;
}

.recipe img {
    border-radius: 50%;
    object-fit: cover;
}

.recipe a:link {
    text-decoration: underline;
    font-size: 1.8rem;
    color: #40b48e;
    cursor: pointer;
}

a:visited {
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
}

a:active {
    text-decoration: underline;
}


