    border-radius: 50px;
    height: 40px;
}

.reference {
    font-size: 20px;
    text-transform: uppercase;
    color: rgb(245, 62, 102);
    /* font-background-color: ; */
    border: none;
    border-radius: 50px;
    height: 40px;
}

.ingredient-list {
    margin-top: 1rem;
    background-color: #f0f0f0;
    border-radius: 0.5rem;
    padding: 0.5rem;
}

.ingredient-list li {
    margin: 0.5rem 0;
}

.ingredient-text {
    font-size: 1.5rem;
    color: #888;
}

.ingredient-weight {
    color: #40b48e;
    font-size: 1.3rem;
}
