.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.navbar {
    background-color: #2c2a2b;
    height: 80px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    position: -webkit-sticky;
    position: sticky;
    z-index: 1;
}

.navbutton {
    width: 50px;
    height: 50px;
    background-color: rgb(247, 43, 43);
    margin-left: 15px;
    z-index: 1;
}

.nav__icon {
    height: 30px;
    width: 30px;
    margin-left: 8px;
    margin-top: 15px;
    color: black;
}

.menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: red;
}

.nav-menu {
    background-color: #060b26;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
    z-index: 1;
}

.nav-menu.active {
    left: 0;
    transition: 350ms;
}

.display__photo {
    height: 130px;
    margin-left: 20%;
    width: 150px;
    align-items: center;
    background: none;
    margin-bottom: 20px;
}

.photo {
    height: 130px;
    width: 100%;
    object-fit: contain;
    border-radius: 100%;
}

.insta__logo {
    text-decoration: none;
    height: 50px;
}

.edit__photo {
    text-decoration: none;
    height: 50px;
}

.nav-text {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
}

.nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}

.nav-text a:hover {
    background-color: #1a83ff;
}

.nav-menu-items {
    width: 100%;
}

.navbar-toggle {
    background-color: #060b26;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}

span {
    margin-left: 16px;
}
