.wrapper {
    height: 60%;
    width: 80%;
    background: rgba( 0, 0, 0, .6);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: -1;
    display: flex;
    flex-direction: column;
}

.wrapper h1 {
    color: seashell;
    margin-top: 15px;
    font-size: 40px;
}

.wrapper p {
    color: rgb(255, 255, 255);
    margin-top: 80px;
    font-size: large;
}

.btn {
    background: no-repeat;
    outline: none;
    border: 1px solid white;
    color: white;
    width: 50%;
    padding: 7px 15px;
    font-size: large;
    cursor: pointer;
    transition: 0.3s ease;
    margin-top: 40%;
    font-family: Georgia, 'Times New Roman', Times, serif;
}

.btn:hover {
    background-color: white;
    color: black;
}
